@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@600&display=swap);
html > body > #root > .App{
  min-width: 100% !important;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #222429 no-repeat;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.App{
  width: 100%; 
  min-height: 100vh;
  font-family: "IBM Plex Mono", sans-serif;
  display: flex;
  flex-direction: column;
}
*{
	--positive_green: #55e383;
	--base_green: #41c7c7;
	--vibe_pink: #FF8B94;
	--alert_red: #FF3939;
	--base_red: #fe6a6a; 
	--base_white: #e0e4e4;
}
.logo-container{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 40px 0;
}
.container{
  width: 320px;
  height: 320px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(80px, 1fr));
  --stagger-delay: 100ms;
  font-family: "IBM Plex Mono", sans-serif;
  color: whitesmoke;
}
.items{ 
  border: 0.5px solid whitesmoke;
  display: grid;
  place-items: center;
  font-size: 30px;
  color: #e0e4e4;
  animation: itementrance 700ms ease;
  animation-fill-mode: backwards;
}
.st{
  grid-column-start: 2;
  display: grid;
  place-items: center;
  animation-delay: calc(1 * var(--stagger-delay));
}
.at{
  animation-delay: calc(2 * var(--stagger-delay));
}
.e{
  grid-row-start: 2;
  grid-column-start: 4;
  animation-delay: calc(3 * var(--stagger-delay));
}
.of{
  grid-row-start: 2;
  color: #41c7c7;
  animation-delay: calc(4 * var(--stagger-delay));
}
.twenty{
  grid-row-start: 3;
  grid-column-start: 4;
  color: #fe6a6a;
  animation-delay: calc(6 * var(--stagger-delay));
}
.twenone{
  grid-row-start: 4;
  grid-column-start: 3;
  color: #fe6a6a;
  animation-delay: calc(7 * var(--stagger-delay));
}
.covid{
  color: wheat;
  border: 0.5px solid whitesmoke;
  grid-row-start: 2;
  grid-column-start: 2;
  grid-column: span 2;
  grid-row: span 2;
  display: grid;
  place-items: end;
  padding: 0 5px;
  font-size: 42px;
  animation: itementrance 700ms ease;
  animation-delay: calc(5 * var(--stagger-delay));
  animation-fill-mode: backwards;
}
.covid p{ 
  max-width: 3ch;
}
@keyframes itementrance{
  from{
    transform: scale(0.3);
    opacity: 0;
  }
  to{
    transform: scale(1);
    opacity: 1;
  }
}

.hero_text{
  font-family: courier;
  line-height: 30px;
  text-align: justify;
  margin: 2em;
  max-width: 50ch;
  color: var(--base_white);
  letter-spacing: 1px;
  font-size: 15px;
}
.hero_text p span{
  font-size: 4em;
}

.navbar {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 8vh;
  display: flex;
  align-items: center;
  border-bottom: 1px dashed wheat;
  width: 100%;
}
.menu-bars {
  font-size: 1.5em;
}
.bars{
  display: flex;
  align-items: center;
  padding: 0 1em;
}
.live{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  border-left: 1px dashed wheat;
  height: inherit;
  width: 100% !important;
  color: #fff;
}
.live div:nth-child(1) p:nth-child(1){
  color:#41c7c7;

}
.live div:nth-child(2) p:nth-child(1){
  color:#fe6a6a;
}
.live div:nth-child(3) p:nth-child(1){
  color:pink;
}
.datas{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
  padding: 1em;
  border-right: 0.5px solid wheat;
}
.datas p{
  font-size: 20px;
  margin: 0;
}

.header{
  width: 100%;
  height: 8vh;
  align-self: flex-start;
  display: flex;
  justify-content: space-between;
}
.header .text{
  border: 1px dashed wheat;
  width: 25%;
  display: grid;
  place-items: center;
}
.text p{
  font-size: 1.5em;
}
.nav-menu {
  z-index: 999;
  background: #222429;
  height: 100vh !important;
  width: 300px;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 1000ms;
  border: 1px dashed wheat;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.nav-menu.active {
  left: 0%;
  transition: 500ms;
}
.nav-menu-item{
  height: 91%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.navbar-toggle{
  list-style: none;
  font-size: 1.5em;
  display: flex;
  justify-content: flex-end;
  margin: 0.5em .5em;
}
.nav-items {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0 1em;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  list-style: none;
  margin-bottom: 1em;
}
.nav-items-inactive{
  color: #41c7c7;
  text-decoration: none;
  font-size: 1.3em;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.nav-items-active{
  color: #94eeee;
}
.nav-items-active p::before{
  content: "> ";
}
.nav-items-inactive span{
  display: block;
  width: 100%;
  height: 3px;
  bottom: 0;
  background: #41c7c7;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
  margin: 0;
  margin-top: -10px;
}
.nav-items-active span{
  background: #94eeee;
}
.nav-items:hover span{
  transform: scaleX(1);
  transform-origin: left;
  transition: transform 0.5s;

}
.nav-items-inactive:hover p{
  text-shadow: rgb(65 199 199 / 90%) 0px 2px 10px;
}
.footy{
  text-align: center;
  color: wheat;
  font-size: 0.8em;
}
@media only screen and (max-width: 250px){
  .nav-menu{
    display: none;
  }
}
@media only screen and (max-width: 450px){
  .datas p{
    font-size: 3vw;
  }
}
.loader-container{
	height: 91vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
  
}
.loading{
	display: grid;
	place-items: center;
  padding: 0 2em;
}
.loading p{
	font-size: 1.6em; 
}
.bouncer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100px;
  height: 100px;
}
.loader-container div:nth-child(3) p{
	font-size: 18px;
}

.bouncer div {
  width: 20px;
  height: 20px;
  background-color: #41cfcf;
  border-radius: 50%;
  animation: bouncer 0.5s cubic-bezier(0.19, 0.57, 0.3, 0.98) infinite alternate;
  /* use https://cubic-bezier.com/ to customize the curve */
}

.bouncer div:nth-child(2) {
  animation-delay: 0.1s;
  opacity: 0.8;
}

.bouncer div:nth-child(3) {
  animation-delay: 0.2s;
  opacity: 0.6;
}

.bouncer div:nth-child(4) {
  animation-delay: 0.3s;
  opacity: 0.4;
}

@keyframes bouncer {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-70px);
  }
}
.leader_container{
	display: grid;
	place-items: center;
	grid-gap: 2em;
	gap: 2em;
	animation: leader 1.5s ease-in;
}

.graph_wrapper{
	width: 100%;
	display: grid;
	justify-items: center;
	border-bottom: 2px solid pink;
}
.graph_wrapper div h2{
	color: var(--vibe_pink);
}

@keyframes leader{
	from{
		-webkit-clip-path: circle(10%);
		        clip-path: circle(10%);
	}
	to{
		-webkit-clip-path: circle(100%);
		        clip-path: circle(100%);
	}
}


.nation_data_wrapper{
	display: grid;
	grid-template-rows: 2fr;
	width: 100% !important;
	font-size: 12px;
	margin: 2em 0;	
	transform-origin: top;

}

.dataA{
	width: 100%;
	display: flex;
	justify-content: space-around;
}
.dataA div{

	
}
.data_test div{
	font-size: 2em;
	display: grid;
	place-items: center;
	text-align: center;
		width: 50%;
	border: 0.8px dashed wheat;
}
.data_recovered div{
	width: 50%;
	border: 0.8px dashed wheat;
	font-size: 1.6em;
	display: grid;
	place-items: center;
	text-align: center;
}
.data_head div{
	width: 100%;
	display: flex;
	justify-content: space-between;
}
.data_cases div, .data_death div{
	width: 33.33%;
	font-weight: bold;
	border: 1px dashed cyan;
	font-size: 2em;
	display: grid;
	place-items: center;
	text-align: center;
}

.country{
	padding: 0 1.5em;
}
.country p:nth-child(1){
	color: whitesmoke;
	font-size: 50px;
	align-self: flex-end;
}
.country p:nth-child(2){
	color: whitesmoke;
	font-size: 20px;
	align-self: flex-end;
}
.data_cases div:nth-child(1) p:nth-child(1){
	color: var(--base_green);
}
.data_cases div:nth-child(2) p:nth-child(1){
	color: var(--base_red);
}
.data_cases div:nth-child(3) p:nth-child(1){
	color: var(--positive_green);
}
.data_death div:nth-child(1) p:nth-child(1){
	color: var(--alert_red);
}
.data_death div:nth-child(2) p:nth-child(1){
	color: var(--base_green);
}
.data_death div:nth-child(3) p:nth-child(1){
	color: var(--alert_red);
}
.data_recovered div:nth-child(1) p:nth-child(1){
	color: var(--vibe_pink);
}
.data_recovered div:nth-child(2) p:nth-child(1){
	color: var(--positive_green);
}
.data_test div:nth-child(1) p:nth-child(1){
	color: var(--base_green);
}
.data_test div:nth-child(2) p:nth-child(1){
	color: var(--vibe_pink);
}


.data_recovered div p:nth-child(2),.data_cases div p:nth-child(2), .data_death div p:nth-child(2),.data_test div p:nth-child(2){
	color: whitesmoke;
}

@keyframes datawrap{
	0%{
		transform: scaleY(0);
	}
	50%{
		transform: scaleY(0.7);
	}
	100%{
		transform: scaleY(1);
	}
}
@media screen and (max-width: 600px){
	.data_head div{
		flex-direction: column;
		align-items: center;
		justify-content: flex-end;
	}
	.nation_data_wrapper{
		font-size: 8px;
	}
}
.graph_container{
	margin: 1em 0.4em;
}

.graph_container div{
	display: flex;
	justify-content: center;
	flex-flow: column wrap;
	align-items: center;
}

.graph_container div h2{
	color: whitesmoke;
}
.graph_container p{
	color: var(--vibe_pink);
	font-size: 22px;
}
.nation_container{
	display: grid;
	justify-items: center;
	padding: 2em 2em;
	overflow: hidden;
}
.nation_header{
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}
.nation_header span:nth-child(1){
	font-size: 1.3em;
	color: whitesmoke;
}
.nation_header span:nth-child(2){
	font-size: 3.5em;
	color: #41c7c7;
}

.nation_form{
	margin: 2em auto;
	display: grid;
	place-items: center;
	outline-style: none;
}
.form_text_input{
	overflow: hidden;
	margin-bottom: 1em;
}
.form_text_input input[type="text"]{
  width: 250px;
  padding: 1em auto 12px;
  background: none;
  color: white;
  font-size: 18px;
  font-weight: 400;
  border: none;
  text-align: center;
  border-bottom: 2px solid #41c7c7;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.nation_button{
	display: flex;
	justify-content: center;
}
.nation_button_submit{
	background: transparent;
	border: 4px solid #41c7c7;
	text-transform: uppercase;
	color: #fff;
	font-size: 1.3em;
	padding: 0.675em 1.35em;
	border-radius: 15px;
	transition: all 0.8s;
	position: relative;
	cursor: pointer;
	overflow: hidden;
}
.nation_button_submit::before{
	content: "";
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 0%;
	background: rgb(42,45,51);
	border-radius: 50% 50% 0 0;
	z-index: -1;
	transition: all 0.4s;
}
.nation_button_submit:hover::before{
	height: 150%;
	transition: all 0.4s;
}
.nation_button_submit:hover{
	color: #41c7c7;
}

.graph_loader{
	color: var(--vibe_pink);
	text-transform: uppercase;
}
.demographic_container{
	display: grid;
	place-items: center;
	grid-row-gap: 2em;
	color: var(--base_white);
	font-size: 18px;
	overflow: hidden;
	width: 100%;
}
.demographic_header,.demographic_sources{
	display: grid;
	justify-items: center;
}
.demographic_header h1{
	color: var(--vibe_pink);
	text-align: center;
	font-size: 1.8em;
}
.demographic_header p{
	text-align: center;
	font-size: 16px;
}
.demographic_hero{
	display: grid;
	place-items: center;
	padding: 1em;
}
.demographic_hero p{
	font-size: 12px;
	color: var(--base_red);
}
.demographic_hero img {
	width: 100%;
	max-width: 640px;
	height: auto;
	object-fit: cover;
	margin-top: 2em;
	border-radius: 8px;
}
.demographic_sources_text p{
	text-align: center;
	color: var(--vibe_pink);
	font-size: 16px;
}
.demographic_sources_img{
	display: flex;
	grid-gap: 20px;
	gap: 20px;
	flex-wrap: wrap;
}
.demographic_sources_img img{
	height: 20vw;
	width: auto;
	object-fit: contain;
}


.performer_wrapper{
    display: grid;
    place-items: center;
    padding: 0 1em;
}
.performer_header_main{
    color: var(--vibe_pink);
    font-size: 2.5em;
    margin-top: 2em;
}
.performer_content{
    position: relative;
    display: grid;
    place-items: center;
    margin: 2em;
}
.performer_section{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: grid;
    place-items: center;
}
.performer_button{
    background: transparent;
    border: 5px solid thistle;
    padding: 1em 1.5em;
    cursor: pointer;
    display: flex;
    justify-content: center;
    white-space: nowrap;
    z-index: 1;
    color: thistle;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-transform: uppercase;
}
.performer_text_header{
    font-size: 2em;
    letter-spacing: 10px;
    font-weight: bolder;
    color: rgb(238, 198, 98);
    font-family: sans-serif;
    text-transform: uppercase;
    transition: opacity 1s ease;
}
.performer_text{
    color: wheat;
    transition: opacity 1.5s ease-in;
    margin: 0;
}
.vanish{
    opacity: 0;
    pointer-events: none;
}
.confetti{
    pointer-events: none;
    position: absolute;
}
.hide{
    display: none;
}
